@use "cams-variables";
@use "colors";

:host ::ng-deep {
  .mill-tabs {
    &__content {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.application-details-page {
  .loader {
    display: flex;
    justify-content: center;
    z-index: 6;
    position: relative;
  }

  &__header,
  &__header-info {
    display: flex;
    align-self: center;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__header {
    gap: 15px;
    padding-block-end: 24px;
    border-bottom: 1px solid colors.$grey-20;
    margin-bottom: 24px;

    &-info {
      gap: 10px;
    }

    &-name {
      font-size: cams-variables.$text-size-heading-large;
      font-weight: bold;
      line-height: 0.9;
      margin: 0;
    }
  }

  .long-title {
    max-width: 148px;
    white-space: pre-wrap;
    word-break: break-word;
    display: block;
  }
}
